<template>
    <div class="element">
        <!-- 头部 -->
        <van-nav-bar
        fixed
        placeholder
        title="测试页"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
        >
            <template #right>
                <van-icon name="plus" size="18" />
            </template>
        </van-nav-bar>
        <!-- 中间部分 -->
        <div class="body">
            <TopMun/>
            <!-- 标签 -->
            <div class="elTag" :class="dynamicTags.length == 0 ? '' : 'border'">
                <p v-show="dynamicTags.length != 0">你选择的过滤条件:</p>
                <el-tag
                class="elTagItem"
                :key="tag"
                size="small"
                v-for="tag in dynamicTags"
                closable
                :disable-transitions="false"
                @close="handleClose(tag)">
                {{tag}} </el-tag>
            </div>
            <div style="margin: 0 0.4rem;">
                <TestList/>
            </div>
        </div>
        <!-- 底部 -->
        <van-tabbar route fixed placeholder>
            <van-tabbar-item replace to="/findF" icon="home-o">首页</van-tabbar-item>
            <van-tabbar-item replace to="/testFind" icon="search">测试页</van-tabbar-item>
        </van-tabbar>
        <!-- 弹出 -->
        <van-popup 
        :close-on-click-overlay="false"
        v-model="show"
        position="left"
        :style="{ height: '100%', width: '80%' }"
        >
            <div style="padding: 0.3rem; height: 87.8%">
                <div style="border-bottom: 1px solid rgb(206, 206, 206)">选择需要的标签</div>
                <div class="popup_body">
                    <van-tree-select
                    :items="items"
                    height='100%'
                    :active-id.sync="activeIds"
                    :main-active-index.sync="activeIndex"
                    /></div>
                <div class="popup_food">
                    <van-row type="flex" justify="space-around">
                        <van-col span="12"><van-button round plain type="info" @click="handleClosePopup">取&nbsp;&nbsp;&nbsp;消</van-button></van-col>
                        <van-col span="12"><van-button round type="info" @click="handleInputConfirm">确&nbsp;&nbsp;&nbsp;认</van-button></van-col>
                    </van-row>
                </div>
            </div>
        </van-popup>
    </div>
</template>
<script>
// import { Toast } from 'vant';
import TopMun from './components/topMun.vue'
import TestList from './components/testList.vue'
export default {
    data() {
        return {
            num: 0,
            active: 0,
            dynamicTags: [],
            show: false,
            items: [
                {text: '123', children: [{text: '温州',id: 1},{text: '杭州',id: 2}]},
                {text: '456', children: [{text: '是啥',id: 3},{text: '又',id: 4}]}
                ],
            inputValue: [],
            activeIds: [],
            activeIndex: 0,
        }
    },
    components:{TopMun,TestList},
    watch: {
        activeIds() {
            let inputVal = []
            let item = this.forItemsChil()
            this.activeIds.forEach(ele => {
                item.forEach(doc => {
                    if (ele == doc.id) {
                        inputVal.push(doc.text)
                    }
                })
            });
            this.inputValue = inputVal
        }
    },
    methods: {
        forItemsChil() {
            let items = []
            this.items.forEach(ele => {
                if (ele.children.length > 0) {
                    ele.children.forEach(item => {
                        items.push(item)
                    })
                }
            })
            return items
        },
        handleClose(tag) {
            this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
            let items = this.forItemsChil()
            items.forEach(item => {
                if (item.text == tag) {
                    this.activeIds.splice(this.activeIds.indexOf(item.id), 1);
                }
            })
        },
        onClickLeft() {
            this.$router.push({name: 'findF'})
        },
        onClickRight() {
            this.show = true
        },
        handleInputConfirm() {
            this.dynamicTags = this.inputValue
            this.show = false
        },
        handleClosePopup() {
            let items = this.forItemsChil()
            let ids = []
            items.forEach(ele => {
                this.dynamicTags.forEach(item => {
                    if (ele.text == item) {
                        ids.push(ele.id)
                    }
                })
            })
            this.activeIds = ids
            this.show = false
        },
    }
}
</script>
<style lang="less" scoped>
.elTag {
    padding: 0.4rem 0.6rem;
    text-align: left ;
    p{
        margin-top: 0;
        margin-bottom: 0.5rem;
        font-size: 12px;
    }
    .elTagItem {
        margin: 0 0.1rem;
    }
}
.border {
    border-top: 1px solid rgb(206, 206, 206);
    border-bottom: 1px solid rgb(206, 206, 206);
}
.popup_body {
    padding: 0.3rem 0;
    height: 100%;
}
.popup_food{
    position: absolute;
    width: 100%;
}
.body{
    height: 85.6%;
   box-sizing: border-box;
   overflow-x: scroll; /* 设置溢出滚动 */
   white-space: nowrap;
   /* 隐藏滚动条 */
   scrollbar-width: none; /* firefox */
   -ms-overflow-style: none; /* IE 10+ */
}
.body::-webkit-scrollbar {
    display: none; /* Chrome Safari */
}

.element {
   height: 100%;
   box-sizing: border-box;
   overflow-x: scroll; /* 设置溢出滚动 */
   white-space: nowrap;
   overflow-y: hidden;
   /* 隐藏滚动条 */
   scrollbar-width: none; /* firefox */
   -ms-overflow-style: none; /* IE 10+ */
}
.element::-webkit-scrollbar {
    display: none; /* Chrome Safari */
}
</style>